@media only screen and (min-width: 820px) {
    .form-container {
        width: 800px;
    }
}

.header {
    padding-bottom: 50px;
}

.main {
    display: flex;
    width: 100%;
    height: 400px;
    background-color: #0096bf3a;
    border-radius: 6px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.footer {
    position: absolute;
    width: 100%;
    bottom: 0px;
    text-align: center;
    left: 0;
}


